import axios from 'axios';
import React from 'react';


const Login = props => {
   
    const [ form, setForm ] = React.useState({
        password: '', email: ''
    })

    const [ reveal, setReveal ] = React.useState(false)
    const { password, email } = form
   
    const onChange = e => setForm({...form, [e.target.name] : e.target.value})
    

    const onSubmit = async e => {
        e.preventDefault()
        if(password.length < 6 && email){
            return alert('Invalid login credentials')
        }
        console.log(password.length)
        if(password && email){
            setReveal(true)
            await axios.post('https://talebvee.net/api/v1/user/login', form, {
                headers: {"Content-Type": "application/json"}
            })
            .then(response => {
                console.log(response.data.data)
                localStorage.setItem('isAuthenticated', JSON.stringify(true))
                window.location.reload()
            })
            .catch(err => {
                alert(err.response.data.error)
                setReveal(false)
            })

        }
        else{
            alert("Password & email address cannot be empty")
        }
        
    }
    

    if(localStorage.isAuthenticated === true){
        return ""
    }

    return (
        <div className="absolutes" style={{background: '#f2f2f2'}}>
            <form className="card" style={{width: '400px'}}>
                <h3 style={{marginTop: 10, marginBottom: 30, textAlign: 'center'}}>Sign In</h3>
                <input type="email" name="email" placeholder="Email Address" onChange={onChange} />
                <input type="password" name="password" placeholder="Enter password" onChange={onChange} />

               
                    <button className="btn float" style={{width: '100%', marginTop: 10, 
                            marginBottom: 5, justifyContent: 'center'
                        }} onClick={onSubmit}>
                             { reveal && <div className="spinner" style={{marginRight: 10}}></div>}
                            Login</button>
             
            </form>
            </div>
    )
}

export default Login
